// ============================== Font 시작 ==============================
.autoplay-progress {
	position: absolute;
	right: 16px;
	bottom: 16px;
	z-index: 10;
	width: 48px;
	height: 48px;
	display: flex;
	align-items: center;
	justify-content: center;
	font-weight: bold;
	color: var(--swiper-theme-color);
	svg {
		--progress: 0;
		position: absolute;
		left: 0;
		top: 0px;
		z-index: 10;
		width: 100%;
		height: 100%;
		stroke-width: 4px;
		stroke: var(--swiper-theme-color);
		fill: none;
		stroke-dashoffset: calc(125.6 * (1 - var(--progress)));
		stroke-dasharray: 125.6;
		transform: rotate(-90deg);
	}
}
h2,
h3,
h4,
.h2,
.h3,
.h4 {
	font-family: 'ChosunSm', serif;
}
h2,
.h2 {
	font-size: 3.2rem;
}
h3,
.h3 {
	font-size: 2.4rem;
}
h4,
.h4 {
	font-size: 2rem;
}
.text {
	word-break: break-all;
	text-align: justify;
	text-indent: 1rem;
	& + .text {
		margin-top: 0.2rem;
	}
}
b {
	font-family: 'ChosunKg', sans-serif;
}
