// ============================== Common 시작 ==============================
.auto-height {
	display: flex;
	flex-direction: column;
	height: calc(100% - 50px);
	gap: 2rem;
	&-component {
		flex: 1;
	}
	&.info {
		gap: 2.4rem;
		height: 100%;
