[class*='-figure'] {
	.img {
		@include img-crop;
		aspect-ratio: 16/9;
		&.horizontal {
			height: calc(100% - 2rem);
			min-height: 16rem;
			aspect-ratio: 0;
		}
	}
	.caption {
		@include pseudo-icon(icon-up-fill);
		font-size: 1.4rem;
		margin-top: 0.4rem;
	}
}
#root {
	position: relative;
	background: #64646422;
	&::after {
		opacity: 0.8;
		user-select: none;
		pointer-events: none;
		content: '';
		display: block;
		width: 100%;
		height: 100%;
		position: absolute;
		left: 0;
		top: 0;
		background: url(../img/paper.png) center top/ 100% auto repeat-y;
	}
	#wrap {
		max-width: 160rem;
		padding: 6rem;
		margin: 0 auto;
	}
}
.progress {
	margin-top: 1.2rem;
	background-color: #ccc;
	height: 0.4rem;
	border-radius: 0.2rem;
	overflow: hidden;
	.value {
		height: 100%;
		width: 1%;
		background-color: #666;
		border-radius: 0.2rem;
		animation: progress 10s linear 0s infinite backwards;
	}
	@keyframes progress {
		from {
			width: 0%;
		}
		80% {
			width: 100%;
		}
		to {
			width: 0%;
		}
	}
}

// 리스트 스타일 시작
[class^='list-style'] {
	li {
		position: relative;
		padding-left: 0.8rem;
		margin-top: 0.4rem;
		&:before {
			display: block;
			position: absolute;
			left: 0;
		}
	}
	&.list-style-bul {
		li {
			&::before {
				content: '';
				width: 0.3rem;
				height: 0.3rem;
				border-radius: 50%;
				background-color: #000;
				top: 0.8rem;
			}
			&.point {
				&::before {
					background-color: #f2f219;
				}
			}
		}
	}
	&.list-style-dash {
		li {
			&::before {
				content: '-';
				width: auto;
				height: auto;
				background: none;
				top: 0;
			}
			&.point {
				&::before {
					background: none;
				}
			}
		}
	}
	&.list-style-star {
		li {
			&::before {
				content: '*';
				width: auto;
				height: auto;
				background: none;
				top: 0;
			}
			&.point {
				&::before {
					background: none;
				}
			}
		}
	}
}
