// ============================== 타이틀 시작 ==============================
.title {
	.pattern {
		display: flex;
		text-align: center;
		justify-content: space-between;
		align-items: flex-end;
		gap: 1rem;
		p {
			font-weight: bold;
			font-family: 'ChosunKm', serif;
		}
		img {
			flex: 1;
			min-width: 30rem;
		}
	}
	&-inner {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		align-items: stretch;
		border-top: 0.2rem solid #000;
		border-bottom: 0.6rem double #000;
		padding: 2rem 0;
		margin-top: 1rem;
		.h1 {
			font-family: 'ChosunKm', serif;
			text-align: center;
			font-size: 8rem;
			text-transform: uppercase;
			flex: 1;
		}
		.ad,
		.date {
			display: flex;
			flex-direction: column;
			justify-content: center;
			align-items: center;
			font-family: 'ChosunKg', sans-serif;
		}
		.ad {
			border: 0.6rem double #000;
			padding: 0 4rem;
		}
		.date {
			border: 0.6rem double #000;
			padding: 0 4rem;
			.month {
				text-align: center;
			}
			.year {
				text-align: center;
				font-size: 4rem;
			}
		}
	}
}
// ============================== 타이틀  끝  ==============================

// ============================== 푸터 시작 ==============================
.footer {
	display: flex;
	gap: 2.4rem;
	margin-top: 2.4rem;
	> * {
		padding: 2rem 0;
	}
	.footer-logo {
		flex: 1;
		flex-basis: 300px;
		border: 0.6rem double #000;
		display: flex;
		align-items: center;
		justify-content: center;
		gap: 1rem;
		img {
			max-width: 36px;
		}
		div {
			p {
				font-family: 'ChosunSm', serif;
			}
		}
	}
	.footer-info {
		flex: 3;
		flex-basis: 900px;
		position: relative;
		border: 0.2rem solid #000;
		text-align: center;
		.btn-account {
			position: absolute;
			left: 2rem;
			top: 2rem;
			width: 120px;
			height: calc(100% - 4rem);
			border: 0.2rem solid #000;
		}
	}
	.btn-shortcut {
		flex: 1;
		flex-basis: 300px;
		border: 0.6rem double #000;
		display: flex;
		align-items: center;
		justify-content: center;
	}
}
.footer-copy {
	margin-top: 2.4rem;
	border-top: 0.2rem solid #000;
	text-align: center;
	padding-top: 2rem;
}
// ============================== 푸터  끝  ==============================

// ============================== 내비게이션바 시작 ==============================
.gnb {
	.depth1 {
		height: 4rem;
		overflow: hidden;
		display: flex;
		justify-content: space-around;
		border-bottom: 0.2rem solid #000;
		font-family: 'ChosunSm', serif;
		text-align: center;
		transition-duration: 0.5s;
		// &:hover {
		// height: 14.4rem;
		// 4 + (2.4 * 4) + 0.8
		// }
		> li {
			position: relative;
			a {
				display: block;
				width: 12rem;
				height: 4rem;
				position: relative;
				text-transform: uppercase;
				span {
					display: block;
					position: absolute;
					left: 50%;
					top: 50%;
					transform: translate(-50%, -50%);
					transition-duration: 0.5s;
					&.ko {
