// ============================== 팝업 시작 ==============================
.ReactModal__Body--open {
	overflow: hidden;
}
.ReactModal__Overlay {
	background-color: #00000090 !important;
}
.modal {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	width: 55vw;
	max-width: 100rem;
	border: solid 0.1rem #5c5c5c;
	padding: 2.4rem;
	display: grid;
	grid-template-areas:
		'modal-title'
		'modal-content'
		'modal-button';
	grid-template-rows: auto 1fr auto;
	gap: 2.4rem;
	background: #a5a5a5;
	&::after {
		opacity: 0.8;
		user-select: none;
		pointer-events: none;
		content: '';
		display: block;
		width: 100%;
		height: 100%;
		position: absolute;
		left: 0;
		top: 0;
		background: url(../img/paper.png) center/ 100% auto repeat-y;
	}
	.modal-title {
		grid-area: modal-title;
		text-align: center;
		position: relative;
		padding-top: 2.4rem;
		.modal-close {
			@include icon(icon-close, 2.8rem);
			position: absolute;
			right: 0;
			top: 0;
		}
	}
	.modal-content {
		grid-area: modal-content;
		overflow-y: auto;
		max-height: 500px;
		&::-webkit-scrollbar {
			display: none;
		}
	}
	.modal-button-area {
		grid-area: modal-button;
		justify-content: center;
		display: flex;
		gap: 0.8rem;
		button {
			max-width: 16rem;
			height: 4.8rem;
			padding: 0.8rem 4rem;
			border: 4px double #000;
		}
		button:first-child:nth-last-child(2),
		button:first-child:nth-last-child(2) ~ button {
			width: 50%;
		}
	}
}
// ============================== 팝업  끝  ==============================

// ============================== 콘텐츠 시작 ==============================
.content {
	display: grid;
	grid-template-columns: 1fr 3fr 1fr;
	gap: 2.4rem;
	padding-top: 4rem;
	> * {
		.title {
			margin-bottom: 1.2rem;
			&.with-viewmore {
				position: relative;
				display: flex;
				justify-content: space-between;
				align-items: center;
				a {
					color: #666;
				}
			}
		}
		.sub-title {
			margin-bottom: 1.2rem;
		}
	}
	> *:not([class*='-figure'], .rooms-text),
	.info-reservation {
		padding-top: 1.6rem;
		border-top: 0.2rem solid #000;
	}

	// ============================== 홈 시작 ==============================
	&.main {
		grid-template-areas:
			'visual-figure visual-figure visual-text'
			'info rooms-tit rooms-tit'
			'info rooms-figure rooms-text'
			'info around faq'
			'news news news';
		.visual-figure {
			grid-area: visual-figure;
			.img img {
				animation: scale 10s linear 0s infinite backwards;
				transform: scale(1.1);
			}
			@keyframes scale {
				from {
					transform: scale(1.1);
				}
				80% {
					transform: scale(1);
				}
				to {
					transform: scale(1.1);
				}
			}
		}
		.visual-text {
			grid-area: visual-text;
			.sub-title {
				word-break: break-all;
			}
			.view-all {
				width: 100%;
				border: 0.6rem double #000;
				justify-content: center;
				padding: 2.4rem 0;
				margin-top: 1.2rem;
				.logo {
					margin-bottom: 0.4rem;
					img {
						max-width: 36px;
					}
					div {
						p {
							font-family: 'ChosunSm', serif;
						}
					}
				}
			}
			.progress {
				margin-bottom: 2rem;
			}
		}

		.rooms-tit {
			grid-area: rooms-tit;
			.tab-title {
				display: flex;
				gap: 1rem;
				align-items: flex-end;
				height: 2.8rem;
				button {
					color: #999;
					transition-duration: 0.5s;
					font-size: 2rem;
					&.active {
						color: #000;
						font-size: 2.4rem;
					}
					span {
						font-size: 1.8rem;
					}
				}
			}
		}
		.rooms-figure {
			grid-area: rooms-figure;
		}
		.rooms-text {
			grid-area: rooms-text;
			> div {
				margin-top: 1.2rem;
				&:first-child {
					margin-top: 0;
				}
				.text {
					margin-top: 0.8rem;
				}
				&.control {
					border: 0.4rem double #000;
					padding: 1.6rem;
					display: flex;
					justify-content: space-between;
					align-items: center;
					margin-bottom: 2rem;
					button {
						@include icon(icon-up-fill, 2.4rem);
						&.prev {
							transform: rotate(-90deg);
						}
						&.next {
							transform: rotate(90deg);
						}
					}
				}
			}
		}

		.info {
			grid-area: info;
			display: flex;
			flex-direction: column;
			justify-content: space-between;
			gap: 2.4rem;
			.info-reservation {
			}
			.info-waytocome {
				img {
					margin-bottom: 1.2rem;
				}
			}
			button {
				border: 0.4rem double #000;
			}
		}

		.around {
			grid-area: around;
			&-list {
				margin-top: 1.2rem;
				display: flex;
				gap: 1.2rem;
				> li {
					flex: 1;
					.around-figure {
						margin-bottom: 0.4rem;
						.img {
							aspect-ratio: 5/2;
						}
					}
					.around-text {
						h4 {
							@include text-ellipsis(1);
							margin-bottom: 0.4rem;
						}
						.source {
							color: #666;
							text-align: right;
							margin: 0.2rem;
							position: relative;
							font-size: 1.4rem;
						}
						.text {
							@include text-ellipsis(2);
						}
					}
				}
			}
		}

		.faq {
			grid-area: faq;
			h4 {
				word-break: break-all;
			}
			button {
				border: 0.4rem double #000;
			}
		}

		.news {
			grid-area: news;
			display: flex;
			flex-wrap: wrap;
			gap: 4rem;
			border-bottom: 0.6rem double #000;
			padding: 2rem 0;
			> li {
				position: relative;
				flex: 1;
				display: flex;
				gap: 1rem;
				&:not(:last-child)::after {
					content: '';
					display: block;
					width: 0.2rem;
					height: 110%;
					position: absolute;
					right: -2rem;
					top: -5%;
					background-color: #000;
				}
				.thumb {
					@include img-crop(16rem, 16rem);
				}
				.cont {
					flex: 1;
					display: flex;
					flex-direction: column;
					justify-content: space-evenly;
					.tit {
						@include text-ellipsis(2);
					}
					.text {
						font-size: 1.4rem;
						@include text-ellipsis(5);
					}
				}
			}
		}
	}
	// ============================== 홈  끝  ==============================

	// ============================== 펜션소개 시작 ==============================
	&.about {
		grid-template-areas:
			'gallery gallery gallery'
			'visual visual visual'
			'waytocome waytocome facilities';

		.gallery {
			grid-area: gallery;
			width: 100%;
			// padding: 0;
			// border: 0;
			// position: relative;
			// overflow: hidden;
			// > img {
			// 	width: 20%;
			// 	min-width: 500px;
			// }
			// .img-list {
			// 	display: flex;
			// 	flex-wrap: nowrap;
			// 	position: absolute;
			// 	left: 0;
			// 	top: 0;
			// 	gap: 20px;
			// 	li {
			// 		min-width: 500px;
			// 	}
			// }
		}

		.visual {
			grid-area: visual;
			text-align: center;
			padding: 6rem 0 3.6rem 0;
		}

		.waytocome {
			grid-area: waytocome;
			.sub-title {
				margin-top: 1.2rem;
			}
		}

		.facilities {
			grid-area: facilities;
			.facility-list {
				display: grid;
				height: calc(100% - 50px);
				grid-template-columns: repeat(2, 1fr);
				text-align: center;
				align-items: center;
				align-content: stretch;
				li {
					justify-self: center;
					i {
						display: block;
						margin: 0 auto;
						width: 40px;
						height: 40px;
						border: 2px solid #000;
						border-radius: 50%;
						margin-bottom: 0.8rem;
					}
					p {
						font-size: 1.4rem;
					}
				}
			}
		}
	}
	// ============================== 펜션소개  끝  ==============================

	// ============================== 객실소개 시작 ==============================
	&.rooms {
		grid-template-columns: repeat(4, 1fr);
		.detail {
			h2 {
				text-align: left;
				.h3 {
					color: #666;
				}
			}
			.roomData-figure {
				overflow: hidden;
				.img {
					img {
						transition-duration: 0.5s;
					}
					img:nth-child(2) {
						left: 100%;
					}
				}
			}
			&:hover {
				.roomData-figure {
					img {
						transform: translateX(-100%);
					}
				}
			}
			.caption {
				text-align: left;
			}
		}
		.map {
			grid-column: 1 /5;
			position: relative;
			.btn-modal button,
			dd button i {
				display: block;
				width: 26px;
				line-height: 26px;
				text-align: center;
				border-radius: 50%;
				font-size: 1.5rem;
				font-family: 'ChosunLo', sans-serif;
				&:hover {
					&:not(button i)::before,
					&:not(button i)::after {
						content: '';
						display: block;
						width: 30px;
						height: 30px;
						position: absolute;
						left: calc(50% - 15px);
						top: calc(50% - 15px);
						border: 1px solid #a9e3ee;
						border-radius: 50%;
						box-sizing: border-box;
						opacity: 0;
						@keyframes ss {
							from {
								transform: scale(1);
								opacity: 0;
							}
							10% {
								opacity: 1;
							}
							to {
								transform: scale(2);
								opacity: 0;
							}
						}
					}
					&:not(button i)::before {
						animation: ss 1s linear 0s infinite;
					}
					&:not(button i)::after {
						animation: ss 1s linear 0.3s infinite;
					}
				}
				&[class^='s'] {
					background-color: #a9e3ee;
				}
				&[class^='c'] {
					background-color: #dddddd;
				}
				&[class^='m'] {
					background-color: #ff7f80;
				}
				&[class^='v'] {
					background-color: #b0c4e9;
				}
				&[class^='p'],
				&.t,
				&.o {
					background-color: #000;
					color: #fff;
				}
			}
			.info-list {
				position: absolute;
				left: 20px;
				bottom: 40px;
				width: 260px;
				padding: 6px;
				background-color: #ffffffd0;
				.info {
					dt {
						h4 {
							font-size: 1.6rem;
							padding: 6px;
							border-radius: 15.5px;
							background-color: #000000af;
							text-align: center;
							color: #fff;
							margin-top: 4px;
						}
					}
					dd {
						display: flex;
						flex-wrap: wrap;
						> * {
							width: 50%;
						}
						button {
							display: flex;
							align-items: center;
							gap: 4px;
							margin-top: 4px;
						}
					}
				}
			}
			.btn-modal {
				button {
					position: absolute;
					transition-duration: 0.5s;
					&.s1 {
						left: 30%;
						top: 68%;
					}
					&.c1 {
						left: 33.5%;
						top: 66.5%;
					}
					&.c2 {
						left: 37%;
						top: 65%;
					}
					&.s2 {
						left: 40.5%;
						top: 63.5%;
					}

					&.v1 {
						left: 31.75%;
						top: 73%;
					}
					&.v2 {
						left: 35.25%;
						top: 71.5%;
					}
					&.m {
						left: 38.75%;
						top: 70%;
					}

					&.s3 {
						left: 57%;
						top: 53%;
					}
					&.c3 {
						left: 63.33%;
						top: 49.66%;
					}
					&.c4 {
						left: 69.66%;
						top: 46.33%;
					}
					&.s4 {
						left: 76%;
						top: 43%;
					}

					&.p1 {
						left: 27%;
						top: 72%;
					}
					&.p2 {
						left: 47%;
						top: 70%;
					}
					&.t {
						left: 51.5%;
						top: 68.5%;
					}
					&.o {
						left: 62%;
						top: 62%;
					}
					&.p3 {
						left: 92%;
						top: 47%;
					}
				}
			}
		}
	}
	// ============================== 객실소개  끝  ==============================

	// ============================== 펜션소식 시작 ==============================
	&.news {
		grid-template-columns: repeat(4, 1fr);
		button {
			text-align: left;
			.news-figure {
				margin-bottom: 1.2rem;
			}
			h3 {
				@include text-ellipsis(2);
				height: 56px;
			}
			.date {
				color: #666;
				text-align: right;
				margin: 0.2rem;
				position: relative;
				font-size: 1.4rem;
				&::before {
					content: '';
					display: block;
					width: calc(100% - 7rem);
					height: 1px;
					border-top: 1px dashed #000;
					position: absolute;
					left: 0;
					top: calc(50% - 0.05rem);
				}
			}
			.text {
				@include text-ellipsis(5);
			}
		}
	}
	// ============================== 펜션소식  끝  ==============================

	// ============================== 예약하기 시작 ==============================
	&.reservation {
		grid-template-columns: 1fr 2fr;
		grid-template-areas:
			'caution info'
			'penalty penalty'
			'shortcut shortcut';
		.caution {
			grid-area: caution;
		}
		.info {
			grid-area: info;
		}
		.penalty {
			grid-area: penalty;
		}
		.shortcut {
			grid-area: shortcut;
			padding: 6rem 2rem;
			border: 0.6rem double #000;
			text-align: center;
		}
	}
	// ============================== 예약하기  끝  ==============================

	// ============================== 주변소개 시작 ==============================
	&.around {
		grid-template-columns: repeat(3, 1fr);
		button {
			text-align: left;
			.news-figure {
				margin-bottom: 1.2rem;
			}
			h3 {
				@include text-ellipsis(1);
			}
			.source {
				color: #666;
				text-align: right;
				margin: 0.2rem;
				position: relative;
				font-size: 1.4rem;
			}
			.text {
				@include text-ellipsis(6);
			}
		}
	}
	// ============================== 주변소개  끝  ==============================

	// ============================== 자주하는질문 시작 ==============================
	&.faq {
		grid-template-areas: 'sort faq-list faq-list';
		.sort {
			grid-area: sort;
			button {
				display: block;
				color: #666;
				margin-bottom: 0.8rem;
				&.active {
					color: #000;
				}
				span {
					font-size: 1.8rem;
				}
			}
		}
		.faq-list {
			grid-area: faq-list;
			.title {
				display: flex;
				justify-content: space-between;
				align-items: flex-end;
				.btns {
					gap: 0.8rem;
					button {
						font-family: 'ChosunSm', serif;
						color: #666;
						&:last-child {
							margin-left: 0.8rem;
						}
					}
				}
			}
		}
	}
	// ============================== 자주하는질문  끝  ==============================
}
// ============================== 콘텐츠  끝  ==============================

table {
	border-top: 0.4rem double #000;
	tbody,
	thead {
		tr {
			th,
			td {
				border-bottom: 0.1rem solid #666;
				padding: 1.2rem 0.8rem;
			}
			th {
				font-family: 'ChosunSm', serif;
			}
		}
		&.alC,
		.alC {
			text-align: center;
		}
	}
}

.accord-wrap {
	display: flex;
	flex-direction: column;
	gap: 0.2rem;
	border-top: 1px solid #000;
	padding-top: 2px;
	.accord-panel {
		border-top: 0.1rem solid transparent;
		border-bottom: 1px solid #000;
		overflow: hidden;
		transition-duration: 0.5s;
		.accord-title {
			display: block;
			width: 100%;
			text-align: left;
			padding: 1rem 1.2rem 1.2rem;
			position: relative;
			&::after,
			&::before {
				content: '';
				display: block;
				position: absolute;
				background-color: #000;
				border-radius: 1px;
			}
			&::before {
				width: 0.2rem;
				height: 2rem;
				right: 2.1rem;
				top: calc(50% - 1.1rem);
				transition-duration: 0.5s;
				opacity: 1;
			}
			&::after {
				width: 2rem;
				height: 0.2rem;
				right: 1.2rem;
				top: calc(50% - 0.2rem);
			}
		}
		.accord-content {
			max-height: 0;
			background-color: #bbb;
			transition-duration: 0.5s;
			transition-timing-function: ease-in-out;
			overflow: hidden;
			p {
				padding: 2.4rem;
			}
		}
		&.active {
			border-color: #000;
			.accord-title {
				&::before {
					transform: rotate(-90deg);
				}
			}
			.accord-content {
				max-height: 200px;
			}
		}
	}
}
